/* Fluid Text with fallback
* 1rem - font size
* .25 (4px) - max font size => 16px+4px=20px
* 20rem (320px) - min width
* 80 (1280px) - max width.
*/
/* Rules for text elements
=====================================================================*/
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: 'Raleway SemiBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  line-height: 1.25;
}
h1 {
  font-family: 'Raleway Black', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 2.25rem;
  font-size: calc(2.25rem + 0.5 * ((100vw - 20rem) / 80));
}
h2 {
  font-family: 'Raleway ExtraBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.875rem;
  font-size: calc(1.875rem + 0.5 * ((100vw - 20rem) / 80));
}
h3 {
  font-family: 'Raleway Bold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.5rem;
  font-size: calc(1.5rem + 0.5 * ((100vw - 20rem) / 80));
}
h4 {
  font-family: 'Raleway SemiBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.375rem;
  font-size: calc(1.375rem + 0.5 * ((100vw - 20rem) / 80));
}
h5 {
  font-family: 'Raleway SemiBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.25rem;
  font-size: calc(1.25rem + 0.5 * ((100vw - 20rem) / 80));
}
h6 {
  font-family: 'Raleway SemiBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.125rem;
  font-size: calc(1.125rem + 0.5 * ((100vw - 20rem) / 80));
}
p {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 1rem;
  font-size: calc(1rem + 0.0625 * ((100vw - 20rem) / 80));
}
address {
  margin-bottom: 16px;
  font-style: normal;
  line-height: inherit;
}
dl {
  margin-top: 0;
  margin-bottom: 16px;
}
dt {
  font-weight: bold;
}
b,
strong {
  font-family: 'Open Sans Bold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-weight: bolder;
}
small {
  font-size: 80%;
}
pre,
code,
kbd,
samp {
  font-family: 'SFMono-Regular', Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
  font-size: inherit;
}
/*===================================================================
  GLOBAL Rules
=====================================================================*/
/* Main part from Reboot.css -
 * https://github.com/twbs/bootstrap/blob/v4-dev/scss/_reboot.scss
 * Also nice advices from -
 * https://justmarkup.com/log/2018/03/collection-of-css-snippets/
 */
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  scroll-behavior: smooth;
}
body {
  overflow-x: hidden /* To hide horizontal scroll from extra margin in row */;
  margin: 0;
  color: #5e606a;
  background-color: #fff;
  font-family: 'Open Sans Regular', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
}
img {
  display: inline-block;
  width: 100%;
  height: auto;
  text-align: center;
  vertical-align: middle;
  word-break: break-all;
  color: inherit;
  font-size: 0.875rem;
  font-style: italic;
}
img::before {
  content: 'Картинка не загрузилась: (' attr(src) ')';
  padding-left: 32px;
}
img::after {
  content: '';
  padding-right: 32px;
}
audio,
video {
  width: 100%;
}
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}
ol,
ul {
  margin-top: 0;
  margin-bottom: 16px;
}
ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}
ol li,
ul li {
  margin-bottom: 8px;
}
abbr[title] {
  cursor: help;
  text-decoration: underline;
  border-bottom: 0;
}
pre {
  overflow: auto;
  margin-top: 0;
  margin-bottom: 16px;
}
figure {
  margin: 0 0 16px;
}
figcaption {
  text-align: center;
}
hr {
  margin-bottom: 32px;
}
::selection {
  color: #000;
  background-color: #883eca;
  text-shadow: none;
}
/* Animations
=====================================================================*/
@-moz-keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@-webkit-keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@-o-keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@-moz-keyframes swing {
  0%, 100% {
    transform: translateY(19px);
  }
  50% {
    transform: translateY(4px);
  }
}
@-webkit-keyframes swing {
  0%, 100% {
    transform: translateY(19px);
  }
  50% {
    transform: translateY(4px);
  }
}
@-o-keyframes swing {
  0%, 100% {
    transform: translateY(19px);
  }
  50% {
    transform: translateY(4px);
  }
}
@keyframes swing {
  0%, 100% {
    transform: translateY(19px);
  }
  50% {
    transform: translateY(4px);
  }
}
/* Forms Elements
=====================================================================*/
form {
  margin-bottom: 32px;
}
fieldset {
  margin: 0 0 16px;
  padding: 8px 16px;
  border-radius: 10px;
}
legend {
  display: block;
  margin-bottom: 8px;
  color: inherit;
  font-size: 1.5rem;
  line-height: inherit;
}
label {
  display: inline-block;
  cursor: pointer;
}
[type='search'] {
  outline-offset: -2px;
  -webkit-appearance: none;
}
select,
[type='radio'],
[type='checkbox'],
[type='color'] {
  margin-right: 8px;
  cursor: pointer;
}
[type='range'] {
  margin: 0;
  cursor: pointer;
}
input[type='file'] {
  cursor: pointer;
  line-height: 1;
}
input,
button,
select,
optgroup,
textarea {
  font-family: inherit;
  font-family: 'Open Sans Italic', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: inherit;
  line-height: inherit;
}
/* Buttons
------------------------------------*/
button,
[type='submit'],
[type='reset'],
[type='button'],
.callForm-btn--submit,
.header-btnModal,
.header-offerBtn,
.subservice-btnModal {
  margin-bottom: 16px;
  padding: 8px 32px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  color: #fff;
  border: 2px solid transparent;
  border-radius: 24px;
  background-color: #fb0275;
  font-family: 'Open Sans Bold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 0.875rem;
  text-transform: uppercase;
}
button:visited,
[type='submit']:visited,
[type='reset']:visited,
[type='button']:visited,
.callForm-btn--submit:visited,
.header-btnModal:visited,
.header-offerBtn:visited,
.subservice-btnModal:visited {
  color: #fff;
}
button:hover,
[type='submit']:hover,
[type='reset']:hover,
[type='button']:hover,
.callForm-btn--submit:hover,
.header-btnModal:hover,
.header-offerBtn:hover,
.subservice-btnModal:hover,
button:focus,
[type='submit']:focus,
[type='reset']:focus,
[type='button']:focus,
.callForm-btn--submit:focus,
.header-btnModal:focus,
.header-offerBtn:focus,
.subservice-btnModal:focus {
  color: #fff;
  border-color: #fff;
  background-color: #883eca;
}
button:active,
[type='submit']:active,
[type='reset']:active,
[type='button']:active,
.callForm-btn--submit:active,
.header-btnModal:active,
.header-offerBtn:active,
.subservice-btnModal:active {
  outline: none;
  transform: translateY(4px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.callForm-btn--submit {
  width: 100%;
}
/* Input fields
------------------------------------*/
[type='text'],
[type='email'],
[type='password'],
[type='search'],
[type='number'],
[type='tel'],
[type='url'],
textarea,
[type='date'],
[type='time'],
[type='datetime-local'],
[type='month'],
[type='week'] {
  padding: 4px 8px;
  cursor: text;
  color: #9babb4;
  border: none;
  border-bottom: 1px solid #9babb4;
  background-color: transparent;
}
[type='text']::-webkit-input-placeholder,
[type='email']::-webkit-input-placeholder,
[type='password']::-webkit-input-placeholder,
[type='search']::-webkit-input-placeholder,
[type='number']::-webkit-input-placeholder,
[type='tel']::-webkit-input-placeholder,
[type='url']::-webkit-input-placeholder,
textarea::-webkit-input-placeholder,
[type='date']::-webkit-input-placeholder,
[type='time']::-webkit-input-placeholder,
[type='datetime-local']::-webkit-input-placeholder,
[type='month']::-webkit-input-placeholder,
[type='week']::-webkit-input-placeholder {
  transition: opacity 0.3s ease;
  opacity: 1;
}
[type='text']:focus::-webkit-input-placeholder,
[type='email']:focus::-webkit-input-placeholder,
[type='password']:focus::-webkit-input-placeholder,
[type='search']:focus::-webkit-input-placeholder,
[type='number']:focus::-webkit-input-placeholder,
[type='tel']:focus::-webkit-input-placeholder,
[type='url']:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder,
[type='date']:focus::-webkit-input-placeholder,
[type='time']:focus::-webkit-input-placeholder,
[type='datetime-local']:focus::-webkit-input-placeholder,
[type='month']:focus::-webkit-input-placeholder,
[type='week']:focus::-webkit-input-placeholder {
  transition: opacity 0.3s ease;
  opacity: 0;
}
textarea {
  overflow: auto;
  width: 100%;
  min-height: 150px;
  padding: 8px 16px;
  resize: vertical;
}
/* Styles for wrapped input fields
------------------------------------*/
.callForm-inputsGroup {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 32px;
}
.callForm-inputsGroup input,
.callForm-inputsGroup textarea {
  width: 100%;
}
.callForm-inputsGroup input + label,
.callForm-inputsGroup textarea + label {
  position: absolute;
  top: -20px;
  left: 8px /* Must be same that padding left on input */;
  transition: opacity 0.3s ease /* Must be same that transition on input */;
  color: #9babb4;
  font-size: 0.875rem;
}
.callForm-inputsGroup input:focus + label,
.callForm-inputsGroup textarea:focus + label {
  opacity: 1;
  color: #000;
}
/* For it's work well input fields must have attribute 'required' */
/* Form state
------------------------------------*/
.formError {
  animation: shake 0.6s ease;
}
.fieldValid {
  -webkit-appearance: none;
  border-radius: 5px;
  box-shadow: 0 0 0 2px #28a745;
}
.fieldError {
  -webkit-appearance: none;
  border-radius: 5px;
  box-shadow: 0 0 0 2px #dc3545;
}
/* Disabled elements
------------------------------------*/
.disabled,
[disabled],
[disabled] + span,
a:visited.disabled,
a.disabled {
  cursor: default;
  text-decoration: none;
  pointer-events: none;
  opacity: 0.7;
  color: #9babb4;
  border-color: #9babb4;
}
.disabled:hover,
[disabled]:hover,
[disabled] + span:hover,
a:visited.disabled:hover,
a.disabled:hover {
  color: #9babb4;
  background-color: #fff;
}
label.disabled {
  background-color: initial;
}
/* Links
=====================================================================*/
a {
  display: inline-block;
  cursor: pointer;
  color: #3594dd;
}
a:visited {
  color: #185a8d;
}
a:hover {
  color: #883eca;
}
a:active {
  outline: none;
  transform: translateY(4px);
  text-shadow: 0 6px 6px rgba(0,0,0,0.23);
}
a.disabled {
  text-decoration: underline;
}
/* Components
=====================================================================*/
/* NoScript
------------------------------------*/
.noscript {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 50px;
  color: #faf8fb;
  background-color: rgba(0,0,0,0.4);
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.noscript-content {
  min-width: 320px;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 16px;
  padding: 8px;
  text-align: center;
}
.noscript-link {
  color: #007bff;
}
.noscript-link:hover {
  text-decoration: none;
}
/* Modal
------------------------------------*/
.modal {
  z-index: -101;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  transition: all ease 0.65s;
  opacity: 0;
  background-color: rgba(0,0,0,0.4);
}
.modal.active {
  z-index: 1001;
  opacity: 1;
}
.modal-innerWrap {
  position: relative;
  overflow: auto;
  min-width: 288px;
  max-width: 480px;
  margin-right: 16px;
  margin-left: 16px;
  padding: 48px 64px;
  color: #fff;
  border-radius: 10px;
  background: url("../images/modal-bg.jpg") no-repeat center/cover;
}
.modal-btnClose,
.modal-btnClose:hover {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 54px;
  height: 48px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  font-size: 0;
}
.modal-btnClose:hover svg,
.modal-btnClose:hover:hover svg {
  fill: #fff;
}
.modal-btnIconClose {
  width: 24px;
  height: 24px;
  fill: #9babb4;
}
/*===================================================================
  Styles for CONTENT
=====================================================================*/
.card {
  margin-bottom: 32px;
}
.card-question {
  margin-bottom: 8px;
  font-family: 'Open Sans Italic', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
}
.card-title {
  margin-top: 0;
  margin-bottom: 8px;
  font-family: 'Raleway SemiBold', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
  font-size: 1.25rem;
  line-height: 1.25;
}
/* Call to action FORM
------------------------------------*/
.callForm {
  margin-bottom: 0;
}
.callForm-helpTxt {
  width: 100%;
  height: 0;
  transition: all 0.6s ease;
  text-align: center;
  opacity: 0;
  color: #9babb4;
  font-size: 0.75rem;
}
.callForm-inputsGroup input:focus {
  border-bottom-width: 3px;
  outline: none;
  font-family: 'Open Sans Bold Italic', system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;
}
.callForm-inputsGroup input:focus ~ .callForm-helpTxt {
  height: auto;
  opacity: 1;
}
.callForm-btn--submit {
  margin-right: 0;
  margin-left: 0;
}
.modal .callForm-inputsGroup input,
.faq .callForm-inputsGroup input {
  color: #faf8fb;
}
.modal .callForm-inputsGroup input::placeholder,
.faq .callForm-inputsGroup input::placeholder {
  color: #faf8fb;
}
.modal .callForm-inputsGroup input:focus,
.faq .callForm-inputsGroup input:focus {
  color: #fff;
  border-bottom-color: #fff;
}
.modal .callForm-inputsGroup input:focus + label,
